.card {
  background-color: black;
  color: white;
  width: 140px;
  height: 120px;
  margin: 10px;
  position: relative;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  border-radius: 5px;
  overflow: hidden;
}

.card-up {
  width: 100%;
  height: auto;
  position:relative;
  overflow: hidden;
  -o-object-fit: cover;
     object-fit: cover;

}

.card-up img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  position: relative;
  top: 0;
  bottom: 0;
  margin: auto;
}




